
'use client';

import { useState, useEffect, useCallback } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
// Slider, Input are no longer directly used for goal setting on this page.

import { Badge } from '@/components/ui/badge';
import { useToast } from '@/hooks/use-toast';
// JournalEditor, SentimentDisplay, ProgressChart are removed
import type { JournalEntry } from '@/types';
// analyzeSentiment and AnalyzeSentimentOutput are removed
import { suggestInsightKeywords, type SuggestInsightKeywordsOutput } from '@/ai/flows/suggest-insight-keywords-flow';
import { categorizeFailureReason, type CategorizeFailureReasonOutput } from '@/ai/flows/categorize-failure-reason-flow';
// format, parseISO are removed as chart is gone
import { Loader2, Send, Lightbulb, AlertTriangle, CheckCircle, XCircle, MessageSquareHeart, Sparkles, Info, Target, ShieldCheck } from 'lucide-react';
import { cn } from '@/lib/utils';
import Link from 'next/link';


const JOURNAL_HISTORY_KEY = 'reflectifyJournalHistory';

// NegativeCharacteristicState and initialCharacteristics removed

export default function ProcessingPage() {
  const [canGoalFail, setCanGoalFail] = useState<boolean | null>(null);

  const [failureReason, setFailureReason] = useState<string>('');
  const [leftHandText, setLeftHandText] = useState<string>('');
  const [rightHandText, setRightHandText] = useState<string>('');
  const [insightsText, setInsightsText] = useState<string>('');
  const [goalImprovementText, setGoalImprovementText] = useState<string>('');
  const [showProcessingResults, setShowProcessingResults] = useState<boolean>(false);

  // journalEntryText, negativeCharacteristics, currentSentiment, chartData states removed

  const [journalHistory, setJournalHistory] = useState<JournalEntry[]>([]);
  const [isLoadingAi, setIsLoadingAi] = useState<boolean>(false); // Used for AI calls and main submit
  const [isProcessingFailure, setIsProcessingFailure] = useState<boolean>(false);
  const { toast } = useToast();

  const [suggestedKeywords, setSuggestedKeywords] = useState<string[] | null>(null);
  const [isSuggestingKeywords, setIsSuggestingKeywords] = useState<boolean>(false);
  const [categorizedFailureOutput, setCategorizedFailureOutput] = useState<CategorizeFailureReasonOutput | null>(null);


  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(JOURNAL_HISTORY_KEY);
      if (storedHistory) {
        const parsedHistory: JournalEntry[] = JSON.parse(storedHistory);
        // Filter for processing entries: either marked as fail-proof or has a failure reason
        const processingEntries = parsedHistory.filter(
          entry => entry.isFailProof === true || (entry.failureReason && entry.failureReason.trim() !== '')
        );
        setJournalHistory(processingEntries);
      }
    } catch (error) {
      console.error("Failed to load or filter journal history from localStorage:", error);
      toast({
        title: "Error",
        description: "Could not load your journal history.",
        variant: "destructive",
      });
    }
  }, [toast]);

  // useEffect for chartData removed

  const handleSetCanGoalFail = (value: boolean) => {
    setCanGoalFail(value);
    setSuggestedKeywords(null); 
    setShowProcessingResults(false);
    setCategorizedFailureOutput(null);
    setLeftHandText('');
    setRightHandText('');
    setInsightsText('');
    setGoalImprovementText('');


    if (value === false) {
      setFailureReason('');
      toast({
        title: "Goal Marked as Fail-Proof",
        description: "Failure analysis section has been hidden.",
        variant: "default",
      });
    } else {
        setInsightsText(''); // Clear insights if switching back to "failure is possible"
    }
  };

  // handleCharacteristicChange removed

  const handleFailureProcessing = useCallback(async () => {
    if (!failureReason.trim()) {
      toast({
        title: 'Missing Information',
        description: 'Please describe how your goal could potentially fail.',
        variant: 'default',
      });
      return;
    }
    setIsProcessingFailure(true);
    setSuggestedKeywords(null);
    setCategorizedFailureOutput(null);
    setShowProcessingResults(false);
    setGoalImprovementText('');

    try {
      const result = await categorizeFailureReason({ failureReason });
      setCategorizedFailureOutput(result);
      setLeftHandText(`${result.categorizedStatement}, inhale exhale.`);
      setRightHandText(`Although ${result.categorizedStatement}, I still want my goal.`);
      setShowProcessingResults(true);
      setInsightsText(''); 
      toast({
        title: 'Processing Complete',
        description: '10F processing results are now available. Please reflect on your insights.',
      });
    } catch (error) {
      console.error('Error categorizing failure reason:', error);
      setLeftHandText(`It is possible to fail because ${failureReason}, inhale exhale.`);
      setRightHandText(`Although it is possible to fail because ${failureReason}, I still want my goal.`);
      setShowProcessingResults(true);
      setInsightsText('');
      toast({
        title: 'Processing Note',
        description: 'Could not categorize failure reason with AI, using basic processing. Please reflect on your insights.',
        variant: 'default',
      });
    } finally {
      setIsProcessingFailure(false);
    }
  }, [failureReason, toast]);

  const handleSuggestKeywords = useCallback(async () => {
    if (!insightsText.trim()) {
      toast({
        title: 'Empty Insight',
        description: 'Please write your insights before suggesting keywords.',
        variant: 'default',
      });
      return;
    }
    setIsSuggestingKeywords(true);
    setSuggestedKeywords(null);
    try {
      const result = await suggestInsightKeywords({ insightText: insightsText });
      setSuggestedKeywords(result.keywords);
      toast({
        title: 'Keywords Suggested',
        description: 'AI has suggested some keywords for your insight.',
      });
    } catch (error) {
      console.error('Error suggesting keywords:', error);
      toast({
        title: 'Keyword Suggestion Error',
        description: 'Could not get keyword suggestions. Please try again.',
        variant: 'destructive',
      });
    } finally {
      setIsSuggestingKeywords(false);
    }
  }, [insightsText, toast]);

  const handleJournalSubmit = useCallback(async () => {
    if (canGoalFail === null) {
      toast({
        title: 'Incomplete Entry',
        description: 'Please select if your goal can potentially fail.',
        variant: 'default',
      });
      return;
    }

    if (canGoalFail === true) {
      if (!failureReason.trim()) {
        toast({
          title: 'Incomplete Entry',
          description: 'Please describe how your goal could potentially fail.',
          variant: 'default',
        });
        return;
      }
      if (showProcessingResults && !insightsText.trim()) {
        toast({
          title: 'Missing Insights',
          description: 'Please provide your insights and realizations from the 10F processing.',
          variant: 'default',
        });
        return;
      }
    }

    setIsLoadingAi(true);
    try {
      const newEntry: JournalEntry = {
        id: new Date().toISOString(),
        date: new Date().toISOString(),
        isFailProof: canGoalFail === false ? true : undefined,
        failureReason: canGoalFail === true && failureReason.trim() ? failureReason.trim() : undefined,
        leftHandText: canGoalFail === true && leftHandText.trim() ? leftHandText.trim() : undefined,
        rightHandText: canGoalFail === true && rightHandText.trim() ? rightHandText.trim() : undefined,
        insightsText: canGoalFail === true && insightsText.trim() ? insightsText.trim() : undefined,
        failureCategory: canGoalFail === true ? categorizedFailureOutput?.category : undefined,
        categorizedFailureStatement: canGoalFail === true ? categorizedFailureOutput?.categorizedStatement : undefined,
        goalImprovementText: canGoalFail === true && goalImprovementText.trim() ? goalImprovementText.trim() : undefined,
        // Ensure other fields not relevant to processing are not set or are undefined
        entryText: '', 
        goalText: undefined,
        goalAchievability: undefined,
        goalDeadline: undefined,
        sentimentAnalysis: undefined,
        negativeCharacteristics: undefined,
      };

      setJournalHistory(prevHistory => {
        // Filter previous history to only include processing entries
        const previousProcessingEntries = prevHistory.filter(
            entry => entry.isFailProof === true || (entry.failureReason && entry.failureReason.trim() !== '')
        );
        const updatedHistory = [newEntry, ...previousProcessingEntries];
        try {
          localStorage.setItem(JOURNAL_HISTORY_KEY, JSON.stringify(updatedHistory));
        } catch (error) {
          console.error("Failed to save journal history to localStorage:", error);
          toast({
            title: "Storage Error",
            description: "Could not save your journal entry due to storage limitations.",
            variant: "destructive",
          });
        }
        return updatedHistory;
      });

      // Reset fields
      setCanGoalFail(null); 
      setFailureReason('');
      setLeftHandText('');
      setRightHandText('');
      setInsightsText('');
      setGoalImprovementText('');
      setShowProcessingResults(false);
      setSuggestedKeywords(null);
      setCategorizedFailureOutput(null);

      toast({
        title: canGoalFail === false ? 'Status Saved' : 'Process Saved',
        description: canGoalFail === false ? 'Your goal has been marked as fail-proof.' : 'Your failure analysis and insights have been recorded.',
      });
    } catch (error) {
      console.error('Error saving processing entry:', error);
      toast({
        title: 'Error',
        description: 'Could not save your processing entry. Please try again.',
        variant: 'destructive',
      });
    } finally {
      setIsLoadingAi(false);
    }
  }, [toast, canGoalFail, failureReason, leftHandText, rightHandText, insightsText, goalImprovementText, showProcessingResults, categorizedFailureOutput, journalHistory]);

  const getCategoryDescription = (category?: string) => {
    if (!category) return null;
    switch (category) {
      case 'POSSIBLE_OUTCOME':
        return 'This failure reason seems to relate to a potential external event or outcome.';
      case 'PAST_TRAIT':
        return 'This failure reason seems to relate to a personal trait or past behavior.';
      case 'ABSOLUTE_TRUTH':
        return 'This failure reason seems to relate to a perceived unchangeable fact or limiting belief.';
      default:
        return null;
    }
  };

  return (
    <div className="container mx-auto max-w-3xl py-8 px-4 flex flex-col gap-8">
      <Card className="shadow-lg rounded-xl overflow-hidden">
        <CardHeader className="bg-primary/30">
          <CardTitle className="text-xl font-semibold text-primary-foreground">
            Impossible to fail
          </CardTitle>
        </CardHeader>
        <CardContent className="pt-6 space-y-6">
          <div className="space-y-3">
            <p className="text-foreground text-base">
              Pay attention to your goal, is it possible to fail?
            </p>
            <div className="flex flex-wrap gap-4 pt-1">
              <Button 
                variant={canGoalFail === true ? 'default' : 'outline'} 
                onClick={() => handleSetCanGoalFail(true)}
                className="flex-grow sm:flex-grow-0"
              >
                <XCircle className="mr-2 h-5 w-5" /> Yes, failure is possible
              </Button>
              <Button 
                variant={canGoalFail === false ? 'default' : 'outline'} 
                onClick={() => handleSetCanGoalFail(false)}
                className={cn(
                  "flex-grow sm:flex-grow-0",
                  canGoalFail === false && "!bg-accent !text-accent-foreground hover:!bg-accent/90"
                )}
              >
                <CheckCircle className="mr-2 h-5 w-5" /> No, it's fail-proof
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {canGoalFail === true && (
        <Card className="shadow-lg rounded-xl overflow-hidden">
          <CardHeader>
            <CardTitle className="text-xl font-semibold text-foreground">
              Standard 10F Process
            </CardTitle>
            <CardDescription className="text-base text-muted-foreground pt-2">
              If your goal could fail, describe how that might happen.
            </CardDescription>
          </CardHeader>
          <CardContent className="pt-6 space-y-4">
            <div className="space-y-2">
              <Label htmlFor="failureReason" className="text-base">How could your goal potentially fail?</Label>
              <Textarea
                id="failureReason"
                value={failureReason}
                onChange={(e) => {
                  setFailureReason(e.target.value);
                  setShowProcessingResults(false); 
                  setCategorizedFailureOutput(null);
                }}
                placeholder="e.g., It is too expensive, I might lose motivation, unexpected events, I am lazy..."
                rows={3}
                className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
              />
            </div>
            <Button 
              onClick={handleFailureProcessing} 
              disabled={isProcessingFailure || !failureReason.trim()}
              className="w-full sm:w-auto bg-accent text-accent-foreground hover:bg-accent/90 focus-visible:ring-ring px-6 py-3 text-base"
              aria-label="Process Potential Failure Mode"
            >
              {isProcessingFailure ? (
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              ) : (
                <Lightbulb className="mr-2 h-5 w-5" />
              )}
              Process Potential Failure
            </Button>
          </CardContent>
        </Card>
      )}

      {canGoalFail === false && (
         <Card className="shadow-lg rounded-xl overflow-hidden border-accent">
            <CardHeader>
              <CardTitle className="text-xl font-semibold text-accent-foreground flex items-center">
                <ShieldCheck className="mr-2 h-6 w-6 text-accent" /> Goal Confidence
              </CardTitle>
            </CardHeader>
            <CardContent className="pt-2">
              <p className="text-base text-foreground">
                Great! You now know your goal is impossible to fail.
              </p>
            </CardContent>
          </Card>
      )}


      {showProcessingResults && canGoalFail === true && (
        <>
        <Card className="shadow-lg rounded-xl overflow-hidden">
          <CardHeader>
            <CardTitle className="text-xl font-semibold text-foreground">10F Processing Results</CardTitle>
            <CardDescription className="text-base text-muted-foreground pt-2">
              Reflect on these statements to strengthen your resolve.
            </CardDescription>
          </CardHeader>
          <CardContent className="pt-6 space-y-6">
            {categorizedFailureOutput?.category && (
              <div className="p-3 bg-primary/10 rounded-md border border-primary/30 text-sm text-primary-foreground flex items-start gap-2">
                <Info className="h-5 w-5 mt-0.5 shrink-0" />
                <p><span className="font-semibold">Process Category:</span> {getCategoryDescription(categorizedFailureOutput.category)}</p>
              </div>
            )}
            <h4 className="text-lg font-semibold text-foreground">Do the 10F processing:</h4>
            <Card className="bg-card border-destructive/50">
              <CardHeader>
                <CardTitle className="text-md font-semibold text-destructive flex items-center">
                  <AlertTriangle className="mr-2 h-5 w-5 text-destructive" /> Left Hand Focus
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-base text-foreground">{leftHandText}</p>
              </CardContent>
            </Card>
            <Card className="bg-card border-accent/50">
              <CardHeader>
                <CardTitle className="text-md font-semibold text-accent-foreground flex items-center">
                  <Lightbulb className="mr-2 h-5 w-5 text-accent" /> Right Hand Affirmation
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-base text-foreground">{rightHandText}</p>
              </CardContent>
            </Card>
          </CardContent>
        </Card>

        <Card className="shadow-lg rounded-xl overflow-hidden">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-foreground flex items-center">
                    <MessageSquareHeart className="mr-2 h-6 w-6 text-primary"/> Insights and Realizations
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                    Is there something you could conclude about this process, about possibilities for its achievement? (Usually something about how you could resolve that barrier). This is the user&apos;s most important part!
                </CardDescription>
            </CardHeader>
            <CardContent className="pt-6 space-y-2">
                <Label htmlFor="insightsText" className="text-base">Your Insights:</Label>
                <Textarea
                    id="insightsText"
                    value={insightsText}
                    onChange={(e) => {
                      setInsightsText(e.target.value);
                      setSuggestedKeywords(null); 
                    }}
                    placeholder="e.g., I realize I can break down the cost, or seek alternative cheaper solutions..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                />
                <div className="mt-4 space-y-3">
                    <Button
                        onClick={handleSuggestKeywords}
                        disabled={isSuggestingKeywords || !insightsText.trim()}
                        variant="outline"
                        size="sm"
                        className="text-sm"
                    >
                        {isSuggestingKeywords ? (
                            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                        ) : (
                            <Sparkles className="mr-2 h-4 w-4" />
                        )}
                        Suggest Keywords
                    </Button>
                    {suggestedKeywords && suggestedKeywords.length > 0 && (
                        <div className="p-3 bg-muted/50 rounded-md border">
                            <h5 className="text-sm font-semibold mb-2 text-foreground">Suggested Keywords:</h5>
                            <div className="flex flex-wrap gap-2">
                                {suggestedKeywords.map((keyword, index) => (
                                    <Badge key={index} variant="secondary" className="text-xs">
                                        {keyword}
                                    </Badge>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </CardContent>
        </Card>

        <Card className="shadow-lg rounded-xl overflow-hidden">
            <CardHeader>
                <CardTitle className="text-xl font-semibold text-foreground flex items-center">
                    <Target className="mr-2 h-6 w-6 text-primary"/> Goal Improvement
                </CardTitle>
                <CardDescription className="text-base text-muted-foreground pt-2">
                    Does the process you did suggest some improvements or better definition of your goal? Should you add something, remove something or change something to make it more attractive and better defined?
                </CardDescription>
            </CardHeader>
            <CardContent className="pt-6 space-y-2">
                <Label htmlFor="goalImprovementText" className="text-base">Your Goal Improvements:</Label>
                <Textarea
                    id="goalImprovementText"
                    value={goalImprovementText}
                    onChange={(e) => setGoalImprovementText(e.target.value)}
                    placeholder="e.g., I could redefine my goal to be more specific about the timeline, or add smaller milestones..."
                    rows={4}
                    className="text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
                />
            </CardContent>
        </Card>
        </>
      )}
      
      <div className="mt-6 flex justify-center">
        <Button 
            onClick={handleJournalSubmit} 
            disabled={isLoadingAi || canGoalFail === null || (canGoalFail === true && (!failureReason.trim() || (showProcessingResults && !insightsText.trim())))}
            className="w-full sm:w-auto bg-primary text-primary-foreground hover:bg-primary/90 focus-visible:ring-ring focus-visible:ring-offset-2 focus-visible:ring-2 px-8 py-3 text-lg rounded-lg shadow-md transition-all duration-150 ease-in-out hover:shadow-lg"
            aria-label={canGoalFail === false ? "Save Fail-Proof Status" : "Save Processing Insights"}
            size="lg"
        >
            {isLoadingAi ? (
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
            ) : (
                <>
                    <Send className="mr-2 h-5 w-5" />
                    {canGoalFail === false ? "Save Status" : "Save Process"}
                </>
            )}
        </Button>
      </div>


      <div className="mt-8 text-center">
        <Button variant="link" asChild>
          <Link href="/">Back to Main Journal Page</Link>
        </Button>
         <Button variant="link" asChild className="ml-4">
          <Link href="/history">View History</Link>
        </Button>
      </div>
    </div>
  );
}

